/* https://stackoverflow.com/questions/39993244/line-drawing-animation-with-pure-css */

  
.progressbar {
    
    width: 100%;
    height: 50px;
    border-radius: 4px;
    padding: 10px;
  }

  .progressbar .progress {
    position: relative;
    /* background-color: green; */
    border-radius: 4px;
    -webkit-transition: width 0.5s ease-in-out;
    -o-transition: width 0.5s ease-in-out;
    transition: width 0.5s ease-in-out;
    height: 100%;
  }

  .progress .useravatar{
      position: absolute;
      right: 0;
      bottom: 0;
  }

  .useravatar{
      width: 40px;
      height: 30px;
  }


