.catalogue {
    margin: 20px;
}

.svgform {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    margin-bottom: 20px;
    margin-top: 20px;
}

.card {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    width: 200px;
    height: 250px;
    border-radius: 5px;
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
}

.category {
    width:  150px;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    margin: 0;
}

.card:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

img {
    border-radius: 5px 5px 0 0;
}

.card img {
    width: 100%;
    height: 100px;
}

.container {
    padding: 2px 16px;
}

.playbutton{
    width: 100px;
    margin: 5px;
    background-color: rgb(63, 117, 153);
    color: white;
    border-radius: 5px;
}


