    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.circle {
    position: relative;
    width: 10%;
    height: 0;
    padding: 5% 0;
    margin: 1em auto;
    border-radius: 50%;
    background: #a7cd80;
    margin-top: 10%;
}

.content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

.content:before {
    content: '';
    vertical-align: middle;
    display: inline-block;
    width: 0;
    height: 100%;
}

.content span {
    vertical-align: middle;
    display: inline-block;
}

.titlequiz {
    font-size: large;
    font-style: bold;
    margin-left: 43%;
}

@-webkit-keyframes spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

