.quizlist{
    padding: 0px;
}

@media only screen and (min-width: 390px) {
    /* For mobile phones: */
    .quizlist{
        padding: 40px;
    }
}

.quizlistitem{
    color: black;
    border-radius: 10px;
    border-width: 2px;
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    background: #fff;
    border-radius: 2px;
    -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
            box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.animatequizitem{
	color: white;
    background: -webkit-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background: -o-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
	background-size: 400% 400%;
	-webkit-animation: GradientQuizTitle 15s ease infinite;
	animation: GradientQuizTitle 15s ease infinite;
}

.quizlistitemtitle{
    text-align: left;
}

.quizlistitemcategory,.quizlistitemplaybtn{
    text-align: right;
    float: right;
}

.quizlistitemstartdate,.quizlistitemdashboardbtn{
	text-align: left;
}

.quizlistitemdashboardbtn{
	visibility: hidden;
}

.quizlistitemfooter{
    margin-top: 20px;
}

.quizlistitemplaybtn{
    margin-right: 10px;
}

@-webkit-keyframes GradientQuizTitle {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes GradientQuizTitle {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}


