
span.svg {
    display: block;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }
  
  .inject-svg {
      width: 75px;
      height: 75px;
      max-width: 75px;
      margin: auto;
    }
  
    .cls-1,.cls-2,.cls-3{
      fill: maroon;
      opacity:0;
    }
  @-webkit-keyframes fireworkone {
    0%   { opacity: 0;}
    25%  { opacity: .15;}
    50%  { opacity: .3;}
    75%  { opacity: .15;}
    100% { opacity: 0;} 
  }
  @keyframes fireworkone {
    0%   { opacity: 0;}
    25%  { opacity: .15;}
    50%  { opacity: .3;}
    75%  { opacity: .15;}
    100% { opacity: 0;} 
  }
  @-webkit-keyframes fireworktwo {
    0%   { opacity: 0;}
    25%  { opacity: .45;}
    50%  { opacity: .8;}
    75%  { opacity: .45;}
    100% { opacity: 0;} 
  }
  @keyframes fireworktwo {
    0%   { opacity: 0;}
    25%  { opacity: .45;}
    50%  { opacity: .8;}
    75%  { opacity: .45;}
    100% { opacity: 0;} 
  }
  @-webkit-keyframes fireworthree {
    0%   { opacity: 0;}
    25%  { opacity: .55;}
    50%  { opacity: 1;}
    75%  { opacity: .55;}
    100% { opacity: 0;} 
  }
  @keyframes fireworthree {
    0%   { opacity: 0;}
    25%  { opacity: .55;}
    50%  { opacity: 1;}
    75%  { opacity: .55;}
    100% { opacity: 0;} 
  }
  
  .firework-icon .cls-1, .firework-icon .cls-2, .firework-icon .cls-3 {
    opacity: 0;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite; }
  
  .firework-icon .cls-3 {
    -webkit-animation-name: fireworthree;
            animation-name: fireworthree;
    -webkit-animation-delay: .11s;
            animation-delay: .11s; }
  
  .firework-icon .cls-2 {
    -webkit-animation-name: fireworktwo;
            animation-name: fireworktwo;
    -webkit-animation-delay: .41s;
            animation-delay: .41s; }
  
  .firework-icon .cls-1 {
    -webkit-animation-name: fireworkone;
            animation-name: fireworkone;
    -webkit-animation-delay: .71s;
            animation-delay: .71s;}
  
  @-webkit-keyframes elastic-pulse {
    0%   { -webkit-transform: scale(0.2); transform: scale(0.2);}
    100% { -webkit-transform: scale(1); transform: scale(1);} 
  }
  
  @keyframes elastic-pulse {
    0%   { -webkit-transform: scale(0.2); transform: scale(0.2);}
    100% { -webkit-transform: scale(1); transform: scale(1);} 
  }
  
  .img-firework {
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: elastic-pulse;
            animation-name: elastic-pulse;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
  }


